// modern-normalize | MIT License | https://github.com/sindresorhus/modern-normalize

html {
  box-sizing: if(variable-exists(root-box-sizing), $root-box-sizing, border-box);
  line-height: 1.15;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

:root {
	-moz-tab-size: 4;
	tab-size: 4;
}

body {
	margin: 0;
	font-family: if(variable-exists(ff-primary), $ff-primary, $ff-system);
	color: if(variable-exists(text-color), $text-color, #000);
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

// h1 {
// 	font-size: 2em;
// 	margin: 0.67em 0;
// }

hr {
	height: 0;
}

abbr[title] {
	text-decoration: underline dotted;
}

b,
strong {
	font-weight: bolder;
}

code,
kbd,
samp,
pre {
	font-family: if(variable-exists(ff-mono), $ff-mono, monospace);
	font-size: 1em;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}


button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button,
select {
	text-transform: none;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
	-webkit-appearance: button;
}

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
	border-style: none;
	padding: 0;
}

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
	outline: 1px dotted ButtonText;
}

fieldset {
	padding: 0.35em 0.75em 0.625em;
}

legend {
	padding: 0;
}

progress {
	vertical-align: baseline;
}

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
	height: auto;
}

[type='search'] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
}

[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit;
}

summary {
	display: list-item;
}
